import React, { Component } from 'react'
import './index.css'
export default class Loading extends Component {
    render() {
        return (
            <div className="inner circle">
                <div className="box1 circle center" ></div>
                <div className="box2 circle center"></div>
                <div className="box3 circle center"></div>
                <div className="box4 circle center"></div>
                <div className="box5 circle center"></div>
                <div className="box6 circle center">
                    <div className="coil" style={{ '--i': '0' }}></div>
                    <div className="coil" style={{ '--i': '1' }}></div>
                    <div className="coil" style={{ '--i': '2' }}></div>
                    <div className="coil" style={{ '--i': '3' }}></div>
                    <div className="coil" style={{ '--i': '4' }}></div>
                    <div className="coil" style={{ '--i': '5' }}></div>
                    <div className="coil" style={{ '--i': '6' }}></div>
                    <div className="coil" style={{ '--i': '7' }}></div>
                </div>
            </div >
        )
    }
}
